import axios from 'axios';
import { req } from './axiosFun';

//查询全部公告
export const findAll=()=>{return req("get","/notice/findAll",{})};

//添加公告
export const addNotice=(params)=>{return req("post","/notice/add/",params)};

//删除用户
export const deleteNotice=(params)=>{return req("get","/notice/delete/"+params,{})};


