import axios from 'axios';
import { req } from './axiosFun';

//获取报修列表
export const GetRepairList=(params)=>{return req("post","/repair/getList",params)};
//获取全部报修分类
export const GetAllClassify=()=>{return req('get',"/repair/getClassify")};
//删除报修记录
export const repairDelete=(params)=>{return req("get","/repair/delete/"+params,{})};
//修改处理状态
export const upState=(params)=>{return req("get","/repair/upState/"+params,{})};




