import axios from 'axios';
import { req,loginreq } from './axiosFun';


//管理员登录
export const adminLogin=(params)=>{return loginreq("post","/user/adminLogin",params)};
//管理员登录
export const adminLogout=()=>{return req("get","/user/adminLogout",{})};


//查询菜单
export const getMenu=()=>{return req("get","/user/getMenu",{})};
//查询角色列表
export const getRoleList=()=>{return req("get","/user/getRoleList",{})};
//获取用户列表
export const GetUserList=(params)=>{return req("post","/user/getUserList",params)};

//添加或修改用户
export const userSave=(params)=>{return req("post","/user/addUser/",params)};

//删除用户
export const userDelete=(params)=>{return req("get","/user/delete/"+params,{})};
//修改用户禁用状态用户
export const setStatus=(params)=>{return req("get","/user/setStatus/"+params,{})};
//重置密码
export const resetPwd=(params)=>{return req("get","/user/resetPwd/"+params,{})};



